<!-- 
    Base calss for inputs buttons
-->
<script>
import BaseComponent from "../BaseComponent";
import ConstantsModals from "../../../constants/ConstantsModals";
import CommonsUtils from "../../../utils/CommonsUtils";

export default {
  extends: BaseComponent,
  name: "BaseInputButton",

  props: {
    // BUTTON
    disabledButton: {
      type: Boolean,
      default: false
    },
    titleSelect: {
      type: String,
      default: ""
    },
    componentView: {
      type: String,
      default: null
    },
    moduleView: {
      type: String,
      default: null
    },
    // INPUT 1
    // Label for input
    label: {
      type: String,
      default: ""
    },
    // Disabled true or false
    disabled: {
      type: Boolean,
      default: false
    },
    // required true or false
    required: {
      type: Boolean,
      default: false
    },
    // size
    size: {
      type: Number,
      default: 0
    },
    // max
    max: {
      type: Number,
      default: 524288
    },
    // min
    min: {
      type: Number,
      default: -1
    },
    // rendered show or not
    rendered: {
      type: Boolean,
      default: true
    },
    // renderLabel show label or not
    renderLabel: {
      type: Boolean,
      default: true
    },
    // Class css for input
    styleClass: {
      type: String,
      default: ""
    },
    // Element contains data for input
    element: {
      type: Object,
      default: null
    },
    // Propertie for set data in element
    property: {
      type: String,
      default: ""
    },
    // Function find data on search
    functionFind: {
      type: Function,
      default: null
    },
    // Function execute after find data on search
    functionAfterFind: {
      type: Function,
      default: null
    },
    // Array names for validators
    validators: {
      type: Object,
      default: null
    },
    // Array names for converters
    converters: {
      type: Object,
      default: null
    },
    // Name for service for find data
    service: {
      type: String,
      default: null
    },
    // Map properties find
    mapPropertiesFind: {
      type: Object,
      default: null
    },
    // INPUT 2
    // Label for input
    label2: {
      type: String,
      default: ""
    },
    // Disabled true or false
    disabled2: {
      type: Boolean,
      default: true
    },
    // required true or false
    required2: {
      type: Boolean,
      default: false
    },
    // size
    size2: {
      type: Number,
      default: 0
    },
    // max
    max2: {
      type: Number,
      default: 30
    },
    // min
    min2: {
      type: Number,
      default: -1
    },
    // rendered show or not
    rendered2: {
      type: Boolean,
      default: true
    },
    // renderLabel show label or not
    renderLabel2: {
      type: Boolean,
      default: true
    },
    // Class css for input
    styleClass2: {
      type: String,
      default: ""
    },
    // Element contains data for input
    element2: {
      type: Object,
      default: null
    },
    // Propertie for set data in element
    property2: {
      type: String,
      default: ""
    },
    // Function find data on search
    functionFind2: {
      type: Function,
      default: null
    },
    // Function execute after find data on search
    functionAfterFind2: {
      type: Function,
      default: null
    },
    // Array names for validators
    validators2: {
      type: Object,
      default: null
    },
    // Array names for converters
    converters2: {
      type: Object,
      default: null
    },
    // Name for service for find data
    service2: {
      type: String,
      default: null
    },
    // Map properties find
    mapPropertiesFind2: {
      type: Object,
      default: null
    }
  },

  methods: {
    /**
     * Method execute when click button
     */
    clickButton: function() {
      const params = {};
      // Add function on select table
      params[
        ConstantsModals.KEY_MODAL_COMPONENT_PARAM_BEAN_SELECT_TABLE
      ] = this;
      params[ConstantsModals.KEY_FOCUS_COMPONENT] = this.getIdFirstInput();
      params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_FUNCTION_SELECT_TABLE] =
        "onSelectTable";

      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self
          .getVueContext()
          .addModal(
            self.titleSelect,
            self.componentView,
            self.moduleView,
            CommonsUtils.isNotNull(self.tab),
            params
          );
      }, self.tab);
    },
    /**
     * Method for get id for first input
     * @returns id for firs input
     */
    getIdFirstInput: function() {
      return this.getId() + "FirtInput";
    },

    /**
     * Method to get number of split properties
     */
    numOfPropertiesSplit: function() {
      return this.property.split("_").length;
    },

    /**
     * Method execute when select data in table
     */
    onSelectTable: function(entity) {
      this.hideToolTips();
      switch (this.numOfPropertiesSplit()) {
        case 1:
          this.element = entity;
          break;
        case 2:
          this.element[this.property.split("_")[0]] = entity;
          break;
      }
    }
  }
};
</script>