<!--
    Component hashtag
-->
<template>
  <div class="InputComponent" v-bind:id="getId()+'Hastag'" :key="getId()">
    <!-- Label -->
    <div class="InputLabelComponent">
      <div v-if="renderLabel">{{label}}</div>
    </div>

    <div class="HashTagComponentContainer">
      <div
        v-for="(hashtag,index) in getArrayHashTagValues()"
        v-bind:key="index"
        class="HashTagComponentContainerChilds HashTagComponentContainerChildsTag"
      >
        <div class="HashTagComponentContainerChildsTagText">{{hashtag}}</div>
        <div
          class="HashTagComponentContainerChildsTagClose"
          v-if="!disabled"
          v-on:click="closeHashtagValue(index)"
        >
          <i class="far fa-times-circle"></i>
        </div>
      </div>

      <div class="HashTagComponentContainerChilds">
        <!-- Input -->
        <input
          autocomplete="off"
          :id="getId()"
          type="hashtag"
          :disabled="disabled"
          :size="realSize"
          :label="label"
          :maxLength="max"
          :minLength="min"
          :placeholder="placeholder"
          :class="getDefaultStyleClass() + ' ' + styleClass"
          v-on:change="onChangeInputWithHandlerViewError"
          v-on:keydown="onKeyDown($event)"
          :value="getValue()"
        />
      </div>
    </div>
  </div>
</template>
<script>
import BaseInputComponent from "../base/baseinput/BaseInputComponent";
import StringUtils from "../../utils/StringUtils";

export default {
  extends: BaseInputComponent,
  name: "HashTagComponent",

  props: {
    // max
    size: {
      type: Number,
      default: 20
    },
    // separator for hastag
    separator: {
      type: String,
      default: ","
    },
    // max length for hasta
    maxLengthHashtag: {
      type: Number,
      default: 3000
    }
  },

  methods: {
    /**
     * Method for get hashtag values
     */
    getArrayHashTagValues: function() {
      const hashtagValue = this.getHashtagValue();
      let hastagValues = [];
      if (StringUtils.isNotBlank(hashtagValue)) {
        hastagValues = hashtagValue.split(this.separator);
      } else {
        hastagValues = [];
      }
      return hastagValues;
    },
    /**
     * Method for close hashtag value
     */
    closeHashtagValue: function(index) {
      let hastagValues = this.getArrayHashTagValues();
      if (!this.disabled) {
        hastagValues.splice(index, 1);
        let value = "";
        hastagValues.forEach(element => {
          value =
            (StringUtils.isNotBlank(value) ? value + this.separator : value) +
            element.trim();
        });
        this.setValueHashTag(value);
      }
    },
    /**
     * Method for set value in element
     * @param value to set
     */
    setValueHashTag: function(value) {
      const modelProperties = this.takeModelProperties();
      const model = modelProperties[0];
      const properties = modelProperties[1];

      if (properties.length <= 1) {
        model[properties[0]] = value;
      } else if (properties.length === 2) {
        model[properties[0]][properties[1]] = value;
      }
    },
    /**
     * Method for fix value resolving find, converters and validators
     */
    fixValue: async function(value) {
      this.resolveDataValue(value);
      document.getElementById(this.getId()).value = "";
    },
    /**
     * Method for set value in element
     * @param value to set
     */
    setValue: function(value) {
      if (StringUtils.isNotBlank(value)) {
        const modelProperties = this.takeModelProperties();
        const model = modelProperties[0];
        const properties = modelProperties[1];

        let hashtagValue = this.getHashtagValue();

        if (StringUtils.isBlank(hashtagValue)) {
          hashtagValue = "";
        }

        hashtagValue =
          (StringUtils.isNotBlank(hashtagValue)
            ? hashtagValue + this.separator
            : hashtagValue) + value.trim();

        if (hashtagValue.trim().length <= this.maxLengthHashtag) {
          if (properties.length <= 1) {
            model[properties[0]] = hashtagValue;
          } else if (properties.length === 2) {
            model[properties[0]][properties[1]] = hashtagValue;
          }
        } else {
          this.addMessageError(
            this.i18n("rfvue.hashtag.lengthError", [this.maxLengthHashtag])
          );
        }
      }
    },
    /**
     * Method for get value for hashtag value in model
     * @returns value for hashtag
     */
    getHashtagValue: function() {
      const modelProperties = this.takeModelProperties();
      const model = modelProperties[0];
      const properties = modelProperties[1];
      let value = null;

      if (properties.length <= 1) {
        value = model[properties[0]];
      } else if (properties.length === 2) {
        value = model[properties[0]][properties[1]];
      }

      return value;
    },
    /**
     * Method for get value for input
     * @returns value for input
     */
    getValue: function() {
      return "";
    },
    /**
     * Mehtod cathc key down event
     * @param event
     */
    onKeyDown: function(event) {
      // if key press is separator force on change
      if (event.key === this.separator) {
        event.preventDefault();
        event.stopPropagation();
        this.fixValue(event.target.value);
        return false;
      }
    },
    /**
     * Method for get type for input
     * @returns hashtag
     */
    getType: function() {
      return "hashtag";
    },
    /**
     * Method for get default class
     * @returns default class for input
     */
    getDefaultStyleClass: function() {
      return "InputTextHashTagComponent";
    }
  }
};
</script>
<style scoped>
.HashTagComponentContainer {
  float: left;
  clear: left;
  width: auto;
  border: 1px solid var(--rf-inputBorderColor, #466289);
  padding: 5px;
  border-radius: 5px;
}
.HashTagComponentContainerChilds {
  float: left;
  width: auto;
  margin-left: 4px;
}

.HashTagComponentContainerChildsTag {
  background: var(--rf-hashtagBackgroundColor, #466289);
  padding: 2px;
  color: var(--rf-hashtagColor, white);
  margin-top: 2px;
  height: 19px;
  border-radius: 5px;
  display: inline-flex;
  padding-top: 6px;
  padding-left: 4px;
  padding-right: 4px;
}

.HashTagComponentContainerChildsTagText {
  text-align: center;
}

.HashTagComponentContainerChildsTagClose {
  margin-left: 4px;
  margin-right: 2px;
  cursor: pointer;
}

.InputTextHashTagComponent {
  float: left;
  clear: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  background: var(--rf-inputBackColor, white);
  color: var(--rf-inputFontColor, black);
  box-sizing: border-box;
  height: 30px;
  border-radius: 5px;
  border: 1px solid var(--rf-inputBorderColor, #466289);
  padding: 6px;
}

.InputTextHashTagComponent[disabled] {
  color: var(--rf-inputFontColorDisabled, black);
  background: var(--rf-inputBackColorDisabled, #afacac);
  opacity: 0.7;
}

.InputTextHashTagComponent:focus {
  border: var(--rf-inputBorderColorFocus, #fff) 1px solid;
  box-sizing: border-box;
  background: var(--rf-inputBackColorFocus, #466289);
  color: var(--rf-inputFontColorFocus, white);
}
</style>