<!-- 
    Input text button component
 -->
<template>
  <div class="InputTextButtonComponent">
    <!-- Input 1 -->
    <InputTextComponent
      v-bind:idView="getIdFirstInput()"
      v-bind:disabled="disabled"
      v-bind:required="required"
      v-bind:rendered="rendered"
      v-bind:element="element"
      v-bind:property="property"
      v-bind:size="size"
      v-bind:max="max"
      v-bind:min="min"
      v-bind:label="label"
      v-bind:service="service"
      v-bind:functionFind="functionFind"
      v-bind:functionAfterFind="functionAfterFind"
      v-bind:mapPropertiesFind="mapPropertiesFind"
      v-bind:tab="tab"
      v-bind:inputButton="getId()"
    />

    <!--Button-->
    <div class="InputTextButtonComponentButton">
      <button
        :id="getId()"
        :disabled="disabledButton"
        class="InputTextButtonComponentButtonButton"
        v-on:click="clickButton"
        tabindex="-1"
      >
        <i class="fas fa-search"></i>
      </button>

      <!-- Input 2 -->
      <InputTextComponent
        v-bind:disabled="disabled2"
        v-bind:required="required2"
        v-bind:rendered="rendered2"
        v-bind:element="element2"
        v-bind:property="property2"
        v-bind:size="size2"
        v-bind:max="max2"
        v-bind:min="min2"
        v-bind:label="label2"
        v-bind:service="service2"
        v-bind:functionFind="functionFind2"
        v-bind:functionAfterFind="functionAfterFind2"
        v-bind:mapPropertiesFind="mapPropertiesFind2"
        v-bind:tab="tab"
      />
    </div>
  </div>
</template>

<script>
import BaseInputButtonComponent from "../base/baseinputbutton/BaseInputButtonComponent";
import InputTextComponent from "../inputtext/InputTextComponent";
export default {
  extends: BaseInputButtonComponent,
  name: "InputTextButtonComponent",
  components: {
    InputTextComponent
  }
};
</script>

<style scoped>
.InputTextButtonComponent {
  margin: 0;
  padding: 0;
  float: left;
  box-sizing: border-box;
}

.InputTextButtonComponentButton {
  margin: 0;
  padding: 0;
  float: left;
  box-sizing: border-box;
}

.InputTextButtonComponentButtonButton {
  width: 30px;
  height: 30px;
  color: var(--rf-inputButtonFontColor, #466289);
  background-color: transparent;
  border: none;
  float: left;
  cursor: pointer;
  margin-top: 23px;
  outline: none;
}
</style>>